@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  background-color: #dbead7;
  text-align: center;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App {
  margin: auto;
  padding: 10px;
  text-align: center;
}

#display {
    font-family: 'Montserrat', sans-serif;
    background-color: #140d0b;
    color: white;
    height: 30px;
    padding: 5px;
    text-align: right;
}

button {
    font-family: 'Montserrat', sans-serif;
    border: none;
    outline: 1px solid #140d0b;
}

.gridContainer {
    display: grid;
    grid-template-columns: 60px 60px 60px 60px;
    grid-template-rows: 50px 50px 50px 50px 50px 5px;
    text-align: center;
    justify-content: center;
    background-color: #140d0b;
}

.number {
    background-color: #6f8767;
    color: #ebebeb;
}

.number:hover {
    color: #140d0b;
}

.operator {
    background-color: #445c3c;
    color: #ebebeb;
}

.operator:hover {
    color: #140d0b;
}

#zero {
    grid-area: 5 / 1 / 6 / 3;
}

#one {
    grid-area: 4 / 1 / 5 / 2;
}

#two {
    grid-area: 4 / 2 / 5 / 3;
}

#three {
    grid-area: 4 / 3 / 5 / 4;
}

#four {
    grid-area: 3 / 1 / 4 / 2;
}

#five {
    grid-area: 3 / 2 / 4 / 3;
}

#six {
    grid-area: 3 / 3 / 4 / 4;
}

#seven {
    grid-area: 2 / 1 / 3 / 2;
}

#eight {
    grid-area: 2 / 2 / 3 / 3;
}

#nine {
    grid-area: 2 / 3 / 3 / 4;
}

#add {
    grid-area: 3 / 4 / 4 / 5;
}

#subtract {
    grid-area: 2 / 4 / 3 / 5;
}

#multiply {
    grid-area: 1 / 4 / 2 / 5;
}

#divide {
    grid-area: 1 / 3 / 2 / 4;
}

#decimal {
    grid-area: 5 / 3 / 6 / 4;
}

#equals {
    grid-area: 4 / 4 / 6 / 5;
    background-color: #216c85;
    color: #ebebeb;
}

#equals:hover {
    color: #140d0b;
}

#clear {
    grid-area: 1 / 1 / 2 / 3;
    background-color: #75260c;
    color: #ebebeb;
}

#clear:hover {
    color: #140d0b;
}

#calculator {
    width: 250px;
    margin: auto;
    border: 2px solid #6f8767;
}

